    color: #d50000;
  } */
/* For image containers */
.wrapper {
    padding-top: 50px;
    background: #ECEFF1;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
  
/* For main background image */
.wrapper {
  background: url(/static/media/background.29557695.jpg) no-repeat fixed center;
  background-repeat: no-repeat;
  background-size: 100%;

}

 
